.authorization__submit {
  border: none;
  border-radius: 3px;
  background-color: rgba(255,255,255,1);
  width: 100%;
  height: 50px;
  font-size: 18px;
  font-weight: 400;
  justify-self: end;
}

.authorization__submit:hover {
  opacity: 0.8;
  transition: opacity 0.5s;
  cursor: pointer;
}